import { Input } from "semantic-ui-react";

const Filter = ({ handleFilterChange, handleClearButton, matcher }) => (
  <div>
    <Input 
      icon='search' 
      iconPosition='left' 
      placeholder='Filter...' 
      onChange={handleFilterChange} 
      value={matcher}
      action={ (matcher) ? {
        icon: 'close',
        onClick: handleClearButton,
      } : null}
      fluid
    />
  </div>
)

export default Filter