import { Button, Form, Input } from "semantic-ui-react";

const PersonForm = ({newName, handleNameChange, newNumber, handleNumberChange, handleSubmit, handleClearName, handleClearNumber}) => (
  <Form className="mw310">
    <Form.Field>
      <Input 
        icon='user' 
        iconPosition='left' 
        placeholder='Name' 
        onChange={handleNameChange} 
        value={newName}
        action={ (newName) ? {
          icon: 'close',
          onClick: handleClearName,
        } : null}
      />
    </Form.Field>
    <Form.Field>
      <Input
        icon='phone square'
        iconPosition='left'
        placeholder='Phone number'
        onChange={handleNumberChange}
        value={newNumber}
        action={ (newNumber) ? {
          icon: 'close',
          onClick: handleClearNumber,
        } : null}
      />
    </Form.Field>
    <Button type="submit" onClick={handleSubmit}>Save</Button>
  </Form>
)

export default PersonForm