import { useEffect, useState } from 'react'
import Filter from './components/Filter'
import PersonForm from './components/PersonForm'
import Persons from './components/Persons'
import Notification from './components/Notification'
import personService from './services/persons'
import { Container } from 'semantic-ui-react'

const App = () => {
  const [persons, setPersons] = useState(null) 
  const [newName, setNewName] = useState('')
  const [newNumber, setNewNumber] = useState('')
  const [matcher, setMatcher] = useState('')
  var latestId = persons?.slice(-1)[0]?.id

  const [notifMessage, setNotifMessage] = useState(null)
  const [hasErrorNotif, setHasErrorNotif] = useState(false)

  // Get initial data
  useEffect(() => {
    personService
      .getAll()
      .then(initialPersons => {
        setPersons(initialPersons)
      })
  }, [])

  if (!persons) {
    return null
  }

  // Helper functions
  const toggleNotif = (action, name) => {
    var message = ''
    switch (action) {
      case "Added":
      case "Deleted":
        message = `${action} ${name}`
        setHasErrorNotif(false)
        break;
      case "Updated":
        message = `${action} ${name}'s number`
        setHasErrorNotif(false)
        break
      case "TypeError":
        message = `Information of ${name} has already been removed from server`
        setHasErrorNotif(true)
        break
      case "ValidationError":
        message = name
        setHasErrorNotif(true)
        break
      default:
        break;
    }
    setNotifMessage(message)
    setTimeout(() => {
      setNotifMessage(null)
    }, 5000)
  }

  const clearFormInputs = () => {
    setNewName('')
    setNewNumber('')
  }

  // Form actions
  const addPerson = (newPersonObject) => {
    personService
      .create(newPersonObject)
      .then(returnedPerson => {
        setPersons([...persons, returnedPerson])
        toggleNotif('Added', returnedPerson.name)
        clearFormInputs()
      })
      .catch(error => {
        toggleNotif('ValidationError', error.response.data.error)
      })
  }

  const deletePerson = (id) => {
    if (window.confirm(`Delete ${persons.find(p => p.id === id).name} ?`)) {
      personService
        .deletePerson(id)
        .then(() => {
          toggleNotif('Deleted', persons.find(person => person.id === id).name)
          setPersons(persons.filter(person => person.id !== id))
        })
        .catch(error => {
          toggleNotif('TypeError', persons.find(person => person.id === id).name)
          setPersons(persons.filter(n => n.id !== id))
        })
    }
  }

  const updatePerson = (id) => {
    const person = persons.find(p => p.id === id)
    const trimmedNewName = newName.trim()
    const trimmedNewNumber = newNumber.trim()

    if (person.number === trimmedNewNumber) {
      alert(`${trimmedNewName} is already added to phonebook with the same number.`)
    } else if (window.confirm(`${trimmedNewName} is already added to phonebook, replace the old number with a new one?`)) {
      const changedPerson = {...person, number: trimmedNewNumber}

      personService
        .update(id, changedPerson)
        .then(returnedPerson => {
          setPersons(persons.map(person => person.id === id ? returnedPerson : person))
          toggleNotif('Updated', returnedPerson.name)
          clearFormInputs()
        })
        .catch(error => {
          // Person already removed from server
          if (error instanceof TypeError) {
            toggleNotif('TypeError', trimmedNewName)
            setPersons(persons.filter(n => n.id !== id))
          } else {
            // ValidationError
            toggleNotif('ValidationError', error.response.data.error)
          }
        })
    }
  }

  const handleNameChange = (e) => {
    setNewName(e.target.value)
  }

  const clearName = () => {
    setNewName('')
  }

  const handleNumberChange = (e) => {
    setNewNumber(e.target.value)
  }

  const clearNumber = () => {
    setNewNumber('')
  }

  const handleSubmit = (e) => {
    e.preventDefault()

    const trimmedNewName = newName.trim()
    const trimmedNewNumber = newNumber.trim()
    const isInPersonsArray = persons.some(person => person.name.toLowerCase() === trimmedNewName.toLowerCase())
    
    if (isInPersonsArray) {
      const personId = persons.find(p => p.name.toLowerCase() === trimmedNewName.toLowerCase()).id
      updatePerson(personId)
    } else {
      addPerson({name: trimmedNewName, number: trimmedNewNumber, id: ++latestId})
    }
  }

  const handleFilterChange = (e) => {
    setMatcher(e.target.value)
  }

  // Filtered list
  const regex = new RegExp(matcher, "i")
  const filteredList = persons.filter(person => regex.test(person.name))

  const clearFilter = () => {
    setMatcher('');
  }

  return (
    <Container>
      <div className='layout'>
      <div className='title'>
        <h1>Phonebook</h1>
      </div>

      <Filter handleFilterChange={handleFilterChange} handleClearButton={clearFilter} matcher={matcher} />

      <section>
        <h2>Add a new contact</h2>
        <PersonForm 
          newName={newName}
          handleNameChange={handleNameChange}
          newNumber={newNumber}
          handleNumberChange={handleNumberChange}
          handleSubmit={handleSubmit}
          handleClearName={clearName}
          handleClearNumber={clearNumber}
        />
      </section>

      <section>
        <Notification message={notifMessage} hasErrorNotif={hasErrorNotif}/>

        <h2>Contact list</h2>
        <Persons 
          list={filteredList}
          handleDelete={deletePerson}
        />
      </section>
      </div>
    </Container>
  )
}

export default App